:root {
  --clr-violet: hsl(263, 55%, 52%);
  --clr-darkgrayblue: hsl(217, 19%, 35%);
  --clr-blackblue: hsl(219, 29%, 14%);
  --clr-white: hsl(0, 0%, 100%);
  --clr-lightgray: hsl(0, 0%, 81%);
  --clr-lightbluegray: hsl(210, 46%, 95%);
  --ff-primary: "Barlow Semi Condensed", sans-serif;
  --fs-primary: calc(13rem / 16);
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

body {
  font-family: var(--ff-primary);
  font-size: var(--fs-primary);
  font-weight: 500;
  line-height: 1.2;
}

/* --------------------------------------------------
   Breakpoint Settings
   -------------------------------------------------- */
/* --------------------------------------------------
   Breakpoint Mixin
   -------------------------------------------------- */
.content-wrapper {
  padding: 24px;
  background-color: var(--clr-lightbluegray);
}
@media (min-width: 64rem) {
  .content-wrapper {
    height: 100vh;
    display: grid;
    grid-template-rows: 1fr auto 1fr;
    grid-template-columns: 1fr auto 1fr;
  }
}

.cards {
  grid-row: 2/3;
  grid-column: 2/3;
  display: grid;
  gap: 24px;
}
@media (min-width: 64rem) {
  .cards {
    grid-template-rows: 282px 282px;
    grid-template-columns: 255px 255px 255px 255px;
    gap: 30px;
  }
}

.card {
  display: flex;
  flex-direction: column;
  background-color: var(--clr-white);
  color: var(--clr-darkgrayblue);
  padding: 33px;
  border-radius: 0.5rem;
}
.card__header {
  display: flex;
  align-items: center;
  gap: 14px;
  margin-bottom: 20px;
}
.card__header__picture {
  border-radius: 9999px;
  height: 28px;
  width: 28px;
  border: 1px solid rgba(255, 255, 255, 0.5);
}
.card__header__title {
  opacity: 0.5;
}
.card__title {
  margin-bottom: 20px;
}
.card__title h2 {
  font-size: 20px;
}
.card__text {
  opacity: 0.7;
  line-height: 1.3;
}
.card--purple {
  background-color: var(--clr-violet);
  color: var(--clr-white);
}
.card--gray {
  background-color: var(--clr-darkgrayblue);
  color: var(--clr-white);
}
.card--blackblue {
  background-color: var(--clr-blackblue);
  color: var(--clr-white);
}
@media (min-width: 64rem) {
  .card1 {
    grid-column: 1/3;
  }
  .card4 {
    grid-column: 2/4;
  }
  .card5 {
    grid-row: 1/3;
    grid-column: 4/5;
  }
}